import svg4everybody from 'svg4everybody';
import $ from 'jquery';
import { WOW } from 'wowjs/dist/wow.js';

import './additional/jquery.vide.min.js';
import './additional/jquery.event.move.js';
import './additional/jquery.twentytwenty.js';
import './additional/slick-conf.js';
