$(document).ready(function() {
    $('.events >span').hide();
    $('.events >span').click(function() {
        $('.events >span').hide();
        $('img.mage_big').slideUp(500);
        // $('.mixSlider .controls').removeClass('is-active');
        // $('.events >span').removeClass('is-active');
    });
    $('.sliderItem img, a.prev, a.next').click(function() {
        // $('.mixSlider .controls').addClass('is-active');
        // $('.events >span').addClass('is-active');
        $('img.mage_big').slideDown(500);
        $('.events >span').show(200);
        var destination2 = $('.js-scrooll-gal').offset().top - 300;
        jQuery("html:not(:animated),body:not(:animated)").animate({
            scrollTop: destination2
        }, 500);
    });
    var index = 1;
    var slCount = $('.mixSlider >div .sliderItem').length
    $('.mixSlider >div .sliderItem').click(function() {
        const attrImg = $(this).children('img:first').attr('src');
        const attrImg2 = $(this).children('img:last').attr('src');
        $('.mixSlider >div >div.controls >.bigImage >img:first').attr('src', attrImg);
        $('.mixSlider >div >div.controls >.bigImage >img:last').attr('src', attrImg2);
        index = $(this).children('img:first').attr('data-index');
    });
    $('.mixSlider >div >div.controls >.events >.prev').click(function(event) {
        event.preventDefault();
        if (index == 1) {
            index = slCount;
            const attrImg = $('#slideImg' + index).attr('src');
            const attrImg2 = $('#slideImg' + index).next().attr('src');
            $('.mixSlider >div >div.controls >.bigImage >img:first').attr('src', attrImg);
            $('.mixSlider >div >div.controls >.bigImage >img:last').attr('src', attrImg2);
        } else {
            index--;
            const attrImg = $('#slideImg' + index).attr('src');
            const attrImg2 = $('#slideImg' + index).next().attr('src');
            $('.mixSlider >div >div.controls >.bigImage >img:first').attr('src', attrImg);
            $('.mixSlider >div >div.controls >.bigImage >img:last').attr('src', attrImg2);
        }
    });
    $('.mixSlider >div >div.controls >.events >.next').click(function(event) {
        event.preventDefault();
        if (index == slCount) {
            index = 1;
            const attrImg = $('#slideImg' + index).attr('src');
            const attrImg2 = $('#slideImg' + index).next().attr('src');
            $('.mixSlider >div >div.controls >.bigImage >img:first').attr('src', attrImg);
            $('.mixSlider >div >div.controls >.bigImage >img:last').attr('src', attrImg2);
        } else {
            index++;
            const attrImg = $('#slideImg' + index).attr('src');
            const attrImg2 = $('#slideImg' + index).next().attr('src');
            $('.mixSlider >div >div.controls >.bigImage >img:first').attr('src', attrImg);
            $('.mixSlider >div >div.controls >.bigImage >img:last').attr('src', attrImg2);
        }
    });
});

// это блок наших услуг
var idThis;
var idShow;
$('.whatWeCan .wrapper-whatWeCan .for-text ul li').mouseenter(function() {
    if ($(window).width() > '760') {
        idThis = $(this).attr('id');
        idShow = '#info-' + idThis;
        $('.for-info').removeClass('active');
        $('#info-' + [idThis]).addClass('active');
        console.log(idShow);
    }
});
$('.whatWeCan .wrapper-whatWeCan .for-text ul li').click(function() {
    idThis = $(this).attr('id');
    idShow = '#info-' + idThis;
    $('.for-info').removeClass('active');
    $('#info-' + [idThis]).addClass('active');
    console.log(idShow);
});
$('.for-info span').click(function(event) {
    $('.for-info').removeClass('active');
});

// это открывание-закрывание мобильного меню
$('.burger-button').click(function() {
    $('.mobil-menu').show(200);
})
$('.mobil-menu span').click(function() {
    $('.mobil-menu').hide(200);
})

//изменение материалов по селектору
$('.mat').hide();
$('#mat1').show();
$(document).ready(function selectedCam(n) {
    console.log('вошли');
    var labelX = n.value;
    if (labelX == 1) {
        $('.mat').hide(200);
        $('#mat1').show(200);
    } else if (labelX == 2) {
        $('.mat').hide(200);
        $('#mat2').show(200);
    } else if (labelX == 3) {
        $('.mat').hide(200);
        $('#mat3').show(200);
    } else {
        $('.mat').hide(200);
        $('#mat1').show(200);
    }
});
// Аккордеон
$(".labelBtn").click(function() {
    $(this).toggleClass('active');
})

// Наверх батон
$(window).scroll(function() {
    if ($(this).scrollTop() > 100) {
        $('.toTopBtn').fadeIn();
    } else {
        $('.toTopBtn').fadeOut();
    }
});

$('.toTopBtn').click(function() {
    $("html, body").animate({
        scrollTop: 0
    }, 600);
    return false;
});

//карты яндекс
ymaps.ready(function() {
    var myMap = new ymaps.Map('map', {
            center: [55.868900, 37.572333],
            zoom: 13
        }, {
            searchControlProvider: 'yandex#search'
        }),

        // Создаём макет содержимого.
        MyIconContentLayout = ymaps.templateLayoutFactory.createClass(
            '<div style="color: #FFFFFF; font-weight: bold;">$[properties.iconContent]</div>'
        ),

        myPlacemark = new ymaps.Placemark(myMap.getCenter(), {
            hintContent: 'Собственный значок метки',
            balloonContent: 'Это красивая метка'
        }, {
            // Опции.
            // Необходимо указать данный тип макета.
            iconLayout: 'default#image',
            // Своё изображение иконки метки.
            iconImageHref: '/pict/geotag.png',
            // Размеры метки.
            iconImageSize: [30, 42],
            // Смещение левого верхнего угла иконки относительно
            // её "ножки" (точки привязки).
            iconImageOffset: [-5, -38]
        });

    myMap.geoObjects
        .add(myPlacemark)
});

$('.clothBlock2 .top').magnificPopup({
  delegate: '.itm a', // child items selector, by clicking on it popup will open
  type: 'image',
  removalDelay: 500,
  callbacks: {
  beforeOpen: function() {
    // just a hack that adds mfp-anim class to markup
     this.st.image.markup = this.st.image.markup.replace('mfp-figure', 'mfp-figure mfp-with-anim');
     this.st.mainClass = this.st.el.attr('data-effect');
    }
  },
  gallery: {
    enabled:true,
    tCounter: '<span class="mfp-counter">%curr% из %total%</span>'
  }
});



